	module.exports = {"data":{"markdownRemark":{"html":"<p>一太郎3.0から使い始めて、<a href=\"/2934\">6.0辺り</a>から徐々に見切りをつけ始めてWordへ6.0の頃から触り始めて、Office97で本格的に使うようになったんだけど、今振り返るともう2007だから10年以上経っているっていうのにまだつい3～4年年前のように感じるのはなんでだろう・・・。まぁ懐古はともかくとして早速使ってみました。 Outlook2007のインストールでちょいとコケたが難なく解決。まず思った感想・・・ めったくそに重たい。ウィンドウがまるでWindowsMediaPlayer11やWindowsLiveMessengerを意識したような形で、見るからに重そうと思ったら実際に重たい。しかも従来みたいなツールバーがなく操作感覚が全然違うので若干違和感。 まぁその辺はやがて慣れるだろうから良しとして今回一番ショッキングだったこと。10年以上に渡りWordやExcelのお供をしていた<strong>イルカのカイル君がついに引退</strong>されたようです。（´；ω；｀）ｳｯ・・・</p>","frontmatter":{"cover":null,"date":"2007-08-06T00:46:00+09:00","strDate":"2007/8/5","title":"Office2007使ってみた"}}},"pathContext":{"path":"/ja/blog/2007/08/06-office2007使ってみた/","langKey":"ja"}}

